.myPhonebook {
  padding: 10px;
  width: 320px;
  border-radius: 4px;
  box-shadow: $main-shadow;
  background-image: $phonebook-bg-color, url(../../images/mobile-phone.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.bigHeader {
  text-align: center;
  margin-bottom: 10px;
  color: $azure-color;
}

.smallHeader {
  margin-top: 20px;
  margin-bottom: 10px;
  color: $azure-color;
}

.subForm {
  padding: 10px;
  margin-bottom: 5px;
  border: 1px solid $white-color;
  border-radius: 5px;
}

.subFormTitle {
  margin-bottom: 10px;
  font-size: 16px;
  color: $white-color;
}

.subFormLabel {
  display: block;
  margin-bottom: 10px;
  font-style: italic;
}

.subFormLabelTitle {
  display: inline-block;
  width: 50px;
  margin-right: 10px;

  color: $white-color;
}

.subFormInput {
  padding: 10px;

  border: 1px solid $white-color;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 500;
  outline: none;

  &::placeholder {
    opacity: 1;
    @include transition(opacity);
  }

  &:focus::placeholder {
    opacity: 0;
  }
}

.addBtn {
  display: block;
  min-width: 100px;
  padding: 5px;

  border: 1px solid $white-color;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 500;
  background-color: $light-blue-color;
  color: $azure-color;
  outline: none;
  cursor: pointer;

  @include transition(color, background-color);

  &:hover {
    background-color: $azure-color;
    color: $black-color;
  }
}

.contacsList {
  @extend .subForm;
  min-height: 100px;
  color: $white-color;
}

.contactItem {
  display: grid;
  grid-template-columns: 20px 1fr 20px;
  grid-template-rows: 1fr 1fr;
  column-gap: 10px;
  align-items: center;
  padding-bottom: 5px;
  margin-bottom: 10px;
  border-bottom: 1px solid $white-color;
}

.box1 {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 3;
}
.box2 {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
}
.box3 {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
}
.box4 {
  grid-column-start: 3;
  grid-row-start: 1;
  grid-row-end: 3;
}

.contactName {
  font-weight: 500;
}

.contactNumber {
  text-align: right;
}

.closeBtn {
  @include flex();
  margin-left: auto;
  padding: 0;
  width: 20px;
  height: 20px;
  font-size: inherit;

  border-radius: 50%;
  background-color: transparent;
  color: $white-color;
  outline: none;
  cursor: pointer;

  @include transition(color, background-color);

  &:hover {
    background-color: white;
    color: red;
  }
}
